<template>

  <div id="app">
      <div class="container">
          <button type="submit" v-on:click="title = 'Hc lp trnh vuejs'">thay i title t component app.vue</button>
          <comp-header
                  v-on:changeTitleEvent="handleChangeTitle"
                  v-bind:title="title"/>
          <list-user
                  v-bind:listUser="listUser"
                  v-on:deleteUserEvent="handleDeleteUser"/>
          <comp-footer v-bind:title="title"/>
      </div>
  </div>

</template>

<!--cc component App-->
            <!--header-->
            <!--listProduct-->
                  <!--product-->
                  <!--product-->
                  <!--product-->
                  <!--product-->
            <!--footer-->
<script>

    import compHeader from './components/compHeader.vue';
    import compFooter from './components/compFooter.vue';
    import listUser from './components/listUser';
    import demoRef from "./components/demoRef";

export default {
  name: 'app',
  data () {
    return {
      title:'Tin Anh vue',
        listUser:[
            { id:100, email:'tienanh@gmail.com',active:false },
            { id:101, email:'tienanh1@gmail.com',active:true },
            { id:102, email:'tienanh2@gmail.com',active:false },
            { id:103, email:'tienanh3@gmail.com',active:true },
            { id:104, email:'tienanh4@gmail.com',active:false },
            { id:105, email:'tienanh5@gmail.com',active:true },
            { id:106, email:'tienanh6@gmail.com',active:true },
        ]
    }
  },
    methods:{
        handleChangeTitle(data){
            // this.title = 'Do thng header thay i'
            this.title = data.title;
            // console.log('handleChangeTitle c gi khi kch hot thnh cng App.vue', data);
        },
        handleDeleteUser(data){
            var indexDelete = -1;
            this.listUser.forEach((u, idx) => {
                // console.log(u.id,idx,data.id);
                if(u.id == data.id){
                    indexDelete = idx;
                }
            });
            if (indexDelete != -1){
                this.listUser.splice(indexDelete,1);
            }
            // console.log('handleDeleteUser trong app.vue', data)
        }
    },
  components:{
      compFooter,
      compHeader,
      listUser,
      demoRef
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1 {
  font-weight: normal;
}

</style>



