<template>
    <header>
        <h1> {{ title }} <br> </h1>
        <button v-on:click="changeTitleHeader">Thay i title t compHeader.vue</button>
    </header>
</template>
<script>
    export default {
        name:'compHeader',
        props:{
            title:String,
        },
        data(){
            return {
            }
        },
        methods: {
            changeTitleHeader: function (e) {
                console.log('Trc khi kch hot changeTitleEvent');

                const data = {
                    title: 'data-tryn qua bin event'
                };
                this.$emit('changeTitleEvent', data);
                console.log('Sau khi kch hot changeTitleEvent');
            }
        }
    }
    /*
        kch hot changTitleHeader trong compHeader.vue
        khi kch hot function bn ngoi app.vue n s chy->
        khi function  chy->thay i d liu
        khi dng nhng s kin mc nh n truyn sn 1 bin event khi mnh t to event mi d liu s truyn
        trong bin this.$emit
     */
</script>
<style>

</style>


